<template>
  <div>
    <v-card elevation="0">
      <v-container grid-list-xs fluid>
        <v-card-title class="justify-center">
          <div style="font-size: 18px; font-weight: bold; color: black">
            History
          </div>
        </v-card-title>

        
          <v-container grid-list-xs class="px-0">
            <v-row
              ><v-col cols="6">
                <v-row justify="center" align="center">
                  <div style="font-size: 15px; font-weight: bold; color: black">
                    Date
                  </div>
                </v-row>
              </v-col>
              <v-col cols="6">
                <v-row justify="center" align="center">
                  <div style="font-size: 15px; font-weight: bold; color: black">
                    Summary
                  </div>
                </v-row>
              </v-col></v-row
            >

            <v-card
              v-for="(item, index) in history_list"
              :key="index"
              class="mt-5 pa-2"
              elevation="0"
              color="#F3F1F5"
              outlined
            >
              <v-container grid-list-xs>
                <v-row>
                  <v-col cols="6"
                    ><v-row justify="center" align="center">{{
                      item.access_date.slice(0, 15)
                    }}</v-row></v-col
                  >
                  <v-spacer></v-spacer>
                  <v-col cols="5" class="">
                    <v-row justify="center" align="center">
                      <div>
                        {{ item.total_case }}
                      </div>
                      <div style="font-weight: bold" class="pl-1">
                        {{ "(" }}
                      </div>
                      <div
                        class="negcovid--text pl-1"
                        style="font-weight: bold"
                      >
                        {{ item.green_case }}
                      </div>
                      <div class="px-1" style="font-weight: bold">
                        {{ " : " }}
                      </div>

                      <div
                        class="pluscovid--text pr-1"
                        style="font-weight: bold"
                      >
                        {{ item.red_case }}
                      </div>
                      <div style="font-weight: bold">
                        {{ ")" }}
                      </div>
                    </v-row>
                  </v-col>
                </v-row>
              </v-container>
            </v-card>
          </v-container>
      
      </v-container>
    </v-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      history_list: null,
    };
  },
  methods: {
    async get_history() {
      await this.axios
        .get(this.$store.state.path_api + "history", {
          headers: this.$store.state.path_header,
        })
        .then((response) => {
          this.history_list = response.data.result;
        });
    },
  },
  async mounted() {
    await this.get_history();
  },
};
</script>

<style lang="scss" scoped></style>
