import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    path_api: "https://www.giantiot.com:8084/api/v1/admins/dashboards/",
    path_header: null,
  },
  mutations: {
    setHeader(state, val) {
      state.path_header = {
      Authorization:
        "Bearer " + val,
    }
    },
  },
  actions: {},
  modules: {},
});
